/* LogStream.css */

.log-container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
    background-color: #1e1e1e; /* 어두운 배경색 */
    color: #dcdcdc; /* 밝은 텍스트 색상 */
    font-family: "Courier New", Courier, monospace; /* 모노스페이스 폰트 */
}

h1 {
    margin: 0 0 20px;
    font-size: 28px;
    color: #4caf50; /* 제목 색상 */
}

.filter-bar {
    margin-bottom: 20px;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

.filter-bar input {
    padding: 10px;
    border: 1px solid #333;
    border-radius: 5px;
    background-color: #2c2c2c;
    color: #fff;
}

.filter-bar input[type="datetime-local"] {
    padding: 0;
}

.log-list {
    flex: 1;
    overflow-y: auto;
    background-color: #2c2c2c; /* 로그 영역 배경색 */
    border: 1px solid #444;
    border-radius: 5px;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.log-item {
    margin-bottom: 15px;
    padding: 15px;
    border-radius: 5px;
}

.log-item.log-level-error {
    background-color: #e74c3c; /* 에러 로그 배경색 */
    color: #fff; /* 에러 로그 텍스트 색상 */
}

.log-item.log-level-info {
    background-color: #3498db; /* 정보 로그 배경색 */
    color: #fff; /* 정보 로그 텍스트 색상 */
}

.log-item.log-level-warning {
    background-color: #f39c12; /* 경고 로그 배경색 */
    color: #fff; /* 경고 로그 텍스트 색상 */
}

.log-timestamp {
    font-size: 12px;
    color: #aaa;
    margin-right: 10px;
}

.log-node {
    font-weight: bold;
    color: #00bcd4;
    margin-right: 10px;
}

.log-level {
    font-weight: bold;
    margin-right: 10px;
}

.log-message {
    margin: 0;
}
